(CNPJ); • Fotografia 3x4; • Estado civil; • Nível de instrução ou escolaridade; • Endereço completo; • Números de telefone, WhatsApp e endereços de e-mail; • Banco, agência e número de contas bancárias; • Bandeira, número, validade e código de cartões de crédito; • Nome de usuário e senha específicos para uso dos serviços do Controlador; • Comunicação, verbal e escrita, mantida entre o Titular e o Controlador. Lê-se Controlador , Fácil Consultoria Imobiliária LTDA Finalidade do uso, tratamento e compartilhamento dos dados O Controlador fica autorizado a compartilhar os dados pessoais do Titular com outros agentes de tratamento de dados, caso seja necessário para as finalidades listadas neste termo, observados os princípios e as garantias estabelecidas pela Lei nº 13.709. O tratamento dos dados pessoais listados neste termo tem as seguintes finalidades: • Possibilitar que o Controlador identifique e entre em contato com o Titular para fins de relacionamento comercial. • Possibilitar que o Controlador analise e obtenha o crédito imobiliário junto a Caixa Econômica Federal, elabore contratos comerciais e emita cobranças contra o Titular, mediante entrega de cópia de seus documentos pessoais, comprante de residencia e informações acerca de sua remuneração para validação do processo de financiamento • Possibilitar que o Controlador verifique pendências financeiras, que o impacte na obtenção do crédito imobiliário e auxilie o cliente em sanar essa pendência Segurança dos dados O Controlador responsabiliza-se pela manutenção de medidas de segurança, técnicas e administrativas aptas a proteger os dados pessoais e a realizar o tratamento dos seguintes dados inseridos pelo Titular, com a intenção de obter a prestação dos serviços ofertados pela mesma, como por exemplo: o crédito imobiliário através do aplicativo que viabiliza a análise de crédito e aprovação junto a CEF, o crédito imobiliário através de um corretor credenciado à Fácil Consultoria Imobiliária, escolha do imóvel Término do Tratamento dos Dados O Controlador poderá manter e tratar os dados pessoais do Titular durante todo o período em que os mesmos forem pertinentes ao alcance das finalidades listadas neste termo. Dados pessoais anonimizados, sem possibilidade de associação ao indivíduo, poderão ser mantidos por período indefinido. O Titular poderá solicitar via e-mail ou correspondência ao Controlador, a qualquer momento, que sejam eliminados os dados pessoais não anonimizados do Titular. O Titular fica ciente de que poderá ser inviável ao Controlador continuar o fornecimento de produtos ou serviços ao Titular a partir da eliminação dos dados pessoais. Direitos do Titular O Titular tem direito a obter do Controlador, em relação aos dados por ele tratados, a qualquer momento e mediante requisição: I - confirmação da existência de tratamento; II - acesso aos dados; III - correção de dados incompletos, inexatos ou desatualizados; IV - anonimização, bloqueio ou eliminação de dados desnecessários, excessivos ou tratados em desconformidade com o disposto na Lei nº 13.709; V - portabilidade dos dados a outro fornecedor de serviço ou produto, mediante requisição expressa, de acordo com a regulamentação da autoridade nacional, observados os segredos comercial e industrial; VI - eliminação dos dados pessoais tratados com o consentimento do titular, exceto nas hipóteses previstas no art. 16 da Lei nº 13.709; VII - informação das entidades públicas e privadas com as quais o controlador realizou uso compartilhado de dados; VIII - informação sobre a possibilidade de não fornecer consentimento e sobre as consequências da negativa; IX - revogação do consentimento, nos termos do § 5º do art. 8º da Lei nº 13.709. Direito de Revogação do Consentimento Este consentimento poderá ser revogado pelo Titular, a qualquer momento, mediante solicitação via e-mail ou correspondência ao Controlador. Tendo o presente Termo sido apresentado ao cliente, assinam o presente termo em duas vias de igual forma e teor e/ou tendo o acesso a esse Termo de forma online, em um dos nossos sistemas, aplicativos e/ou publicações em redes sociais, compete o cliente, clicar no aceite do Termo, para validar os procedimentos apresentados acima.")]),_c('v-card-actions',[_c('v-spacer'),_c('v-btn',{attrs:{"color":"green darken-1","text":""},on:{"click":function($event){_vm.dialog = false}}},[_vm._v(" Fechar ")])],1)],1)],1)],1),_c('v-text-field',{staticStyle:{"max-width":"250px"},attrs:{"counter":40,"rules":_vm.nameRules,"label":"Nome","required":""},model:{value:(_vm.name),callback:function ($$v) {_vm.name=$$v},expression:"name"}}),_c('v-text-field',{staticStyle:{"max-width":"250px"},attrs:{"rules":_vm.emailRules,"label":"E-mail","required":""},model:{value:(_vm.email),callback:function ($$v) {_vm.email=$$v},expression:"email"}}),_c('v-text-field',{directives:[{name:"mask",rawName:"v-mask",value:('(##)####-#####'),expression:"'(##)####-#####'"}],staticStyle:{"max-width":"250px"},attrs:{"rules":_vm.phoneRules,"label":"Telefone Celular","required":""},model:{value:(_vm.telefone),callback:function ($$v) {_vm.telefone=$$v},expression:"telefone"}}),_c('v-container',{attrs:{"fluid":""}},[_c('v-checkbox',{scopedSlots:_vm._u([{key:"label",fn:function(){return [_c('div',[_vm._v(" Eu concordo com os "),_c('v-tooltip',{attrs:{"bottom":""},scopedSlots:_vm._u([{key:"activator",fn:function(ref){
